import 'uni-pages';import Vue from 'vue'
import App from './App'

Vue.config.productionTip = false

App.mpType = 'app'

//全局挂载
Vue.prototype.serverURL = "http://facetoface.top:9001/app/"

// Vue.prototype.serverDemoURL = "http://192.168.1.158:9001/demo/"

Vue.prototype.serverDemoURL = "http://facetoface.top:9001/demo/"


Vue.prototype.getGlobalUser = function(key) {
	var userInfo = uni.getStorageSync("globalUser");
	if (userInfo != null && userInfo != "" && userInfo != undefined) {
		return userInfo;
	} else {
		return null;
	}
}

const app = new Vue({
    ...App
})
app.$mount()